.container.featured {
	max-width: 1200px;
	margin: 0 auto;
	position: relative;

	h1 {
		font-size: 1.8em;
	}

	.featured {
		border: 30px solid #fff;
		box-shadow: 1px 1px 2px $neutral_light_25, -1px -1px 2px $neutral_light_25;
	}

	&:before {
		content: '';
		background: $primary;
		position: absolute;
		width: 100vw;
		top: 0;
		left: -5vw;
		height: 100px;
		z-index: -1;
	}
}

@media(min-width: 40em) {
	.container.featured {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		.featured {
			width: 100%;
		}

		h1 {
			background: transparent;
			color: $neutral;
			flex: 1 1 200px;
			margin-right: 10px;
			max-width: 400px;
		}

		.content {
			border-left: 10px solid $primary;
			padding-left: 20px;
			margin: 20px 0;
			flex: 2 1 300px;
		}
	}
}

@media(min-width: 1333px) {
	.container.featured {
		&:before {
			left: calc(600px - 50vw);
		}
	}
}